.overlay-background {
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  background-color: rgba(44,62,80,0.8);
  padding: 2em;
  z-index: 9999999;
}
.overlay-background .overlay-wrapper {
  display: block;
  width: 100%;
  margin: 0 auto;
}
.overlay-background .overlay-close {
  display: block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #fc4349;
  font-size: 3em;
  position: absolute;
  top: 20px;
  right: 20px;
}
@media only screen and (min-width: 768px) {
  .overlay-background .overlay-wrapper {
    width: 60%;
  }
}
@media only screen and (min-width: 1024px) {
  .overlay-background .overlay-wrapper {
    width: 50%;
  }
}


