.form-button {
  display: block;
  width: 100%;
  height: 50px;
  line-height: 47px;
  border-radius: 5px;
  margin: 2em 0;
  text-transform: uppercase;
  background-color: #fc4349;
  border: 1px solid #dc040b;
  border-bottom-width: 4px;
  color: #fff;
}
.form-button.form-button-white {
  background-color: #fff;
  border-color: #b3b3b3;
  color: #fc4349;
}
.form-button:active {
  border-bottom-width: 1px;
  line-height: 50px;
}
.form-group {
  display: block;
  width: 100%;
  height: 100px;
  overflow: hidden;
  margin-bottom: 1em;
  text-align: center;
}
.form-group label {
  display: block;
  font-weight: normal;
  font-size: 0.9em;
  height: 30px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  color: #fc4349;
}
.form-group .form-input {
  display: inline-block;
  width: 30%;
  height: 70px;
  padding: 0.5em;
  font-size: 1.5em;
  border: none;
  outline: none;
  border-radius: 5px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}


